* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(17, 17, 17);
  color: rgb(240, 145, 145);
  font-weight: 900;
}

.header {
  background-color: rgb(44, 145, 218);
  color: rgb(14, 13, 14);
  padding: 15px;
}

img {
  max-width: 50px;
}

img:hover {
  max-width: 100px;
  transition: all 1s ease 1s;
  padding: 20px;
  border: 4px solid white;
  border-radius: 50%;
  background: rgb(240, 145, 145);
}
